* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
  }
  
  .App {
    text-align: center;
  }
  
  main {
    background: linear-gradient(to bottom, #75757525 80%, #20202000), url("/src/Images/image2.jpg") center no-repeat;
    background-attachment: fixed;
    width: 100%;
    height: 85vh; /* Change to vh for viewport height */
    display: flex;
    align-items: center;
    justify-content: center;
    overflow: hidden; /* Ensures the image does not overflow the main container */
    position: relative;
  }
  
  main .placement {
    margin-top: -25px;
    background-color: #91919156;
    backdrop-filter: blur(5px);
    height: 25%;
    width: 40%;
    display: flex; /* Changed to flex for better responsiveness */
    align-items: center;
    justify-content: center;
    gap: 1rem;
    letter-spacing: 2px;
    flex-direction: row;
  }
  
  /* Responsive design for mobile */
  @media (max-width: 768px) {
    main .placement {
      width: 75%; /* Full width on smaller screens */
      height: auto; /* Adjust height to auto for better fitting */
      margin-top: 0; /* Remove negative margin */
    }
  
    main .slogan h1 {
      font-size: 1.8rem; /* Adjust font size for mobile */
    }
  
    main .slogan h3 {
      font-size: 1.2em; /* Adjust font size for mobile */
    }
  
    main .button_placement {
      margin: 5vh; /* Increase margin on mobile */
    }
  
  
    
  }
  
  main .slogan h3 {
    font-size: 1.5em;
    font-weight: 900;
    color: #ffffff;
    text-align: center;
    text-transform: capitalize;
  }
  
  main .slogan h1 {
    text-transform: capitalize;
    color: #181818;
    text-align: center;
    font-size: 2.5rem;
  }
  
  main .slogan:hover {
    transform: scale(1.05);
  }
  
  main .button_placement {
    position: absolute;
    margin: 2vh; 
  }
  
  main .button_placement button {
    -webkit-border-radius: 28px;
    border-radius: 28px;
    text-shadow: 2px 1px 3px #666666;
    box-shadow: 0px 1px 3px #666666;
    font-family: Georgia;
    color: #ffffff;
    font-size: 20px;
    background: #94480d;
    padding: 10px 20px;
    border: solid #1f628d 2px;
    text-decoration: none;
  }
  
  main .button_placement button:hover {
    background: #fc893c1a;
    text-decoration: none;
  }
  .newsletter
{
    width: auto;
    height: 55dvh;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 18rem;
    background: url("/src/Images/image3.jpg") center;
    background-attachment: fixed;
}
.newsletter .container_letter
{
    display: flex;
    gap: 2rem;
    flex-direction: column;
}

 
.newsletter .container_letter h1{
    color: #fcfcfe;
    font-size: 3rem;
}

.newsletter .container_letter form 
{
    display: flex;
    position: relative;
}
.newsletter .container_letter form input

{
    color: #f3f5f4;
    width: 450px;
    height: 50px;
    padding: 15px;
    font-size: 1.3rem;
    outline: none;
    border: none;
background: rgba(223, 223, 223, 0.123);
box-shadow: 0 4px 30px rgba(68, 37, 20, 0.164);
backdrop-filter: blur(3.7px);
position: relative;
}
.newsletter .container_letter form::after {
    content: "";
    position: absolute;
    bottom: 0px;
    left: 0;
    width:450px;
    height: 3px;
    scale: 0;
    transition: 0.5s ease-in-out;
    transform-origin: left;
    background-color: #0099ff;
}
.newsletter .container_letter  form:hover::after 
{

    scale: 1;
    transition: 0.5s ease-in-out;

}

.newsletter .container_letter form button
{
    width: 150px;
    height: 50px;
    font-size: 20px;
    color: #fff;
    border: none;
    cursor: pointer;
        background-image: linear-gradient(to right, #FF512F 0%, #F09819  51%, #FF512F  100%);
        background-size: 200% auto;
        transition: 0.5s;
              
}

.newsletter .container_letter form button:hover
{
    background-position: right center; /* change the direction of the change here */
    color: #fff;
    box-shadow: 0 0 20px #444;
}
